@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,300;1,400;1,500;1,600;1,700;1,800&display=swap);
html {
  font-family: 'Open Sans', sans-serif;
}

body {
  margin: 0;
  padding: 0;
  background-color: #f7f7f7;
  overflow-x: hidden;
}

li {
  list-style-type: none;
}

a {
  text-decoration: none;
}

.nav-menu {
  padding: 40px 0 30px 0;
  margin: -20px 0 0 0;
  width: 100vw;
  display: flex;
  align-items: center;
  font-family: 'Montserrat', sans-serif;
  font-size: 12px;
  background-color: white;
  border: 2px solid rgb(230, 226, 226);
}

.menu-container {
  margin-left: auto;
  display: flex;
  align-items: center;
}

.menu-li {
  margin: 2.2px 15px 0 5px;
  color: rgb(95, 95, 95);
}

.logo {
  font-family: 'Montserrat', sans-serif;
  color: rgb(0, 132, 255);
  margin: 0 20px 0 40px;
  font-size: 35px;
  height: 50px;
  width: 50px;
}

.active {
  color: rgb(134, 0, 0);
  text-decoration: underline;
}

* {
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
}

.profile-container {
  width: 90vw;
  max-width: 1080px;
  display: flex;
  justify-content: space-between;
  margin: 1rem auto;
}

.profile-missions,
.profile-rockets {
  width: 47%;
}

h2 {
  padding: 0.5rem;
}

.profile-table {
  border: 1px solid grey;
  border-radius: 2px;
}

.profile-table > li {
  border-bottom: 1px solid grey;
  padding: 1rem;
}

.profile-table > li:last-child {
  border: none;
}

.rocket-card-container {
  display: flex;
  margin: 20px;
  font-family: sans-serif;
}

.rocket-img {
  height: 180px;
  width: 210px;
}

.info-container {
  display: flex;
  flex-direction: column;
  padding: 10px;
}

.rocket-title {
  margin: -16px 0 0 0;
}

.rocket-description {
  margin: 10px 0 0 9px;
  line-height: 30px;
}

.reserve-button {
  width: 190px;
  height: 50px;
  margin: 20px 0 0 9px;
  background-color: #007bff;
  color: white;
  font-size: 14px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.reserve-button:hover,
.reserve-button:active {
  background-color: #056bd8;
  transition: 0.5s;
}

.reserved-span {
  background-color: #18a2b8;
  padding: 7px;
  color: white;
  border-radius: 5px;
  font-size: 11px;
  font-weight: bolder;
  transition: 0.5s;
}

.reserved-button {
  width: 190px;
  height: 50px;
  margin: 20px 0 0 9px;
  background-color: white;
  color: gray;
  font-size: 14px;
  border: 1px solid rgb(116, 116, 116);
  border-radius: 5px;
  cursor: pointer;
  transition: 0.5s;
}

table,
th,
td {
  border: 1px solid rgb(230, 226, 226);
  border-collapse: collapse;
  padding: 0.6rem 0.4rem;
}

button {
  border-radius: 3px;
  font-size: 14px;
  cursor: pointer;
}

.list-container {
  margin: 2rem auto;
  width: 90vw;
  max-width: 1900px;
}

th {
  text-align: left;
}

td {
  vertical-align: top;
}

.button-container {
  vertical-align: middle;
  padding: 0 1rem;
}

span {
  white-space: nowrap;
}

.button-status {
  padding: 0.2rem 0.3rem;
  font-weight: 600;
  border: none;
  background-color: grey;
  color: white;
  cursor: inherit;
}

.button-join {
  border: 1.5px solid black;
  padding: 0.4rem 0.4rem;
}

tr:nth-child(even) {
  background-color: rgb(240, 240, 240);
}

.button-status-active {
  background-color: rgb(0, 165, 165);
}

.button-join-active {
  border: 1.5px solid red;
  color: red;
}

